
import {defineComponent, ref} from 'vue'
import {useMouse} from "@/hooks/useMouse"
import {useDate} from "@/hooks/useDate"
import {useCounter} from "@/hooks/useCounter"
import {useLocalStorage, useSessionStorage} from "@/hooks/useStorage"

interface Data {

}

export default defineComponent({
  name: "Hooks",
  components: {},
  props: {},
  setup() {
    let {x, y} = useMouse()
    let {now} = useDate()
    let current = ref<number>(0)
    // let {getMessage, setMessage, clearMessage} = useLocalStorage('user-message', 'Hello~')
    let {getMessage, setMessage, clearMessage} = useLocalStorage('user-obj', {name: 'Hello~'})
    let {count, inc, dec, set, reset, delayInc, delayDec,stepDec,stepInc} = useCounter(current.value, {min: 0, max: 100})
    return {
      x,
      y,
      now,
      count,
      inc,
      dec,
      reset,
      set,
      delayInc,
      delayDec,
      stepInc,
      stepDec,
      setMessage,
      clearMessage,
      getMessage
    }
  }
})
